// 85400 nnnn
export function formateTimeStamp(timeStamp) {
  let day
  let hour
  let min
  let seconds

  day = parseInt(timeStamp / (24 * 60 * 60)) // 
  const afterDay = timeStamp - day * 24 * 60 * 60 // 
  hour = parseInt(afterDay / (60 * 60)) // 
  const afterHour = timeStamp - day * 24 * 60 * 60 - hour * 60 * 60 // 
  min = parseInt(afterHour / 60) // 
  seconds = parseInt(timeStamp - day * 24 * 60 * 60 - hour * 60 * 60 - min * 60) // 

  if (day < 10) {
    day = '0' + day
  }

  if (hour < 10) {
    hour = '0' + hour
  }

  if (min < 10) {
    min = '0' + min
  }

  if (seconds < 10) {
    seconds = '0' + seconds
  }

  const restStamp = {
    day: day,
    hour: hour,
    min: min,
    seconds: seconds
  }
  return restStamp
}
