<template>
  <p style="line-height: 1.5rem;" v-html="countTxt"></p>
</template>

<script>
import { formateTimeStamp } from '@/utils/format.js'
export default {
  props: {
    time: { type: [Number] }
  },
  data() {
    return {
      times: {},
      countTxt: ''
    }
  },
  methods: {
    // 
    mistiming() {
      let timeStamp = (this.time - new Date().getTime()) / 1000
      this.times = formateTimeStamp(timeStamp)
      const str = ` <span class="d">${this.times.day}</span><span class="h">${this.times.hour}</span><span class="m">${this.times.min}</span><span class="s">${this.times.seconds}</span>`
      this.countTxt = str
      const TimeDown = setInterval(() => {
        if (timeStamp > 0) {
          timeStamp--
          const newTime = formateTimeStamp(timeStamp)
          const str = ` <span class="d">${newTime.day}</span><span class="h">${newTime.hour}</span><span class="m">${newTime.min}</span><span class="s">${newTime.seconds}</span>`
          this.countTxt = str
        } else {
          this.countTxt = ''
          clearInterval(TimeDown)
        }
      }, 1000)
    }
  },
  mounted() {
    this.$nextTick(() => {
      this.mistiming()
    })
  }
}
</script>

<style>
.d,
.h,
.m,
.s {
  background: #c71c60;
  color: #fff;
  line-height: 49px;
  font-size: 32px;
  font-family: Arial;
  padding: 0 5px;
  margin: 5px;
  border-radius: 5px;
}
</style>
